import { EventEmitter } from "@angular/core";
import { ChatbotBase } from "./chatbot.abstract";
import { IButtonNode, IButtonNodeCondition } from "../../chatflow-reader/logics/button.logic";

export class ChatbotButton extends ChatbotBase<IButtonNode>{
  public clicked$ = new EventEmitter<IButtonNodeCondition>();

   onClick(data: IButtonNodeCondition){
    this.clicked$.emit(data)
  }
}