import { Directive, EventEmitter } from "@angular/core";
import { ChatbotBase } from "./chatbot.abstract";
import { take, timer } from "rxjs";
import { ISpeakNode } from "../../chatflow-reader/logics/speak.logic";

@Directive()
export class ChatbotAnswer extends ChatbotBase<ISpeakNode>{
    public loadingFinished$ = new EventEmitter<boolean>();

    ngOnInit(): void {
        if(this.data.isLoading === false){
            this.loadingFinished$.emit(true);
            return;
        }
        const source$ = timer(this.data.loadingSec * 1000);
        source$.pipe(take(1)).subscribe(d=>{
            this.data.isLoading = false;
            this.loadingFinished$.emit(true);
        })
        
    }
}