import { take } from "rxjs";
import { NodeLogicAbstract } from "../classes/node-logic.abstract";

export interface ISpeakNode{
  text: string;
  isLoading: boolean;
  loadingSec: number;
}

export class SpeakLogic extends NodeLogicAbstract<ISpeakNode> {
    start(): void {
        const debugLogger = (window as any).debugLogger;
        if (debugLogger) {
            debugLogger.info('ì±—í”Œë¡œìš°-Speak', `ğŸ—£ï¸ Speak ë…¸ë“œ ì‹œì‘`);
            debugLogger.info('ì±—í”Œë¡œìš°-Speak', `   í…ìŠ¤íŠ¸: "${this.readData.data.text}"`);
            debugLogger.info('ì±—í”Œë¡œìš°-Speak', `   ë¡œë”©: ${this.readData.data.isLoading}, ${this.readData.data.loadingSec}ì´ˆ`);
        }
        
        let speakText = this.readData.data.text;
        
        // ë³€ìˆ˜ ì¹˜í™˜: {{{variableName}}} í˜•ì‹
        const regex = /\{\{\{(.+?)\}\}\}/g;
        const matches = speakText.matchAll(regex);

        for (const match of matches) {
            const fullMatch = match[0];
            const variableName = match[1];
            if(this.logicReader.variables[variableName]){
                speakText = speakText.replace(fullMatch, this.logicReader.variables[variableName]);
                if (debugLogger) {
                    debugLogger.info('ì±—í”Œë¡œìš°-Speak', `   ë³€ìˆ˜ ì¹˜í™˜: {{{${variableName}}}} â†’ ${this.logicReader.variables[variableName]}`);
                }
            }
        }
        
        if (debugLogger) {
            debugLogger.info('ì±—í”Œë¡œìš°-Speak', `   ìµœì¢… í…ìŠ¤íŠ¸: "${speakText}"`);
            debugLogger.info('ì±—í”Œë¡œìš°-Speak', `   ì±—ë´‡ ë©”ì‹œì§€ ìƒì„± ì¤‘...`);
        }
        
        const ref = this.chatbotActionService.createChatComponent(
            this.chatbotActionService.chatStyleSetting.answer, 
            {
                text: speakText, 
                isLoading: this.readData.data.isLoading, 
                loadingSec: this.readData.data.loadingSec
            }
        );
        
        ref.instance.loadingFinished$.pipe(take(1)).subscribe(d=>{
            if (debugLogger) {
                debugLogger.success('ì±—í”Œë¡œìš°-Speak', `âœ… Speak ë…¸ë“œ ì™„ë£Œ â†’ ë‹¤ìŒ ë…¸ë“œë¡œ ì´ë™`);
            }
            this.end();
        })
    }
}