import { ComponentRef, ElementRef, inject, Injectable, Type, ViewContainerRef } from "@angular/core";
import { Router } from "@angular/router";
import { ChatbotBase } from "../classes/chatbot.abstract";
import { Subject } from "rxjs";
import { ChatbotAnswer } from "../classes/chatbot-answer.class";
import { ChatbotButton } from "../classes/chatbot-button.class";
import { ChatbotQuestion } from "../classes/chatbot-question.class";


type TChatStyleSettings = {
  answer: Type<ChatbotAnswer>;
  question: Type<ChatbotQuestion>;
  button: Type<ChatbotButton>;
  [key: string]: Type<ChatbotBase<any>>;
};
@Injectable({providedIn: 'root'})
export class ChatbotActionService {
  public question: string = '';

  // public behaviorManager!: BehaviorManager;
  // public scenarios: KeyValue<string, any[]>[] = [];
  // public currentScenarios: any[] = [];
  // public behaviorType: string = 'blog';

  public chatbotScrollBody!: ElementRef;
  public chatbotContent!: ViewContainerRef;
  public chatStyleSetting: TChatStyleSettings = {
    answer: ChatbotAnswer,
    question: ChatbotQuestion,
    button: ChatbotButton
  }
  // public chatbotTextArea!: HTMLTextAreaElement;
  private router = inject(Router);
  public questionCreated$: Subject<string> | null = null;
  public messageCreated$ = new Subject<void>(); // ë©”ì‹œì§€ ìƒì„± ì•Œë¦¼

  public createChatComponent<D, T extends ChatbotBase<D>>(
    component: Type<T>,
    data?: D
  ): ComponentRef<T> {
    const ref = this.chatbotContent.createComponent(component);
    const instance = ref.instance;
    if (data) {
      instance.data = data;
    }
    this.messageCreated$.next(); // ë©”ì‹œì§€ ìƒì„± ì•Œë¦¼
    return ref;
  }
  
}